<template>	
	<div class="search">
		<el-autocomplete class="search-txt" v-model="companyName" :fetch-suggestions="querySearchAsync" placeholder="请输入公司名称" @select="handleSelect" clearable>
		</el-autocomplete>
		<button @click="selectBtn" class="search-btn" type="button" name="btn">
		搜索 <span v-if="show" style="margin-left: 4px">→</span></button>
	</div>
</template>
<script type="text/javascript">
	import reqData from "../reqData.js"
	export default{
		name:'search',
		data(){
			return{
				companyNames:{},
				companyName:'',
				companyList:[]
			}
		},
		props:['show'],
		created: function(){
		},
		inject:  ['reload'],
		// watch:{
		// 	companyName:function (newQuestion, oldQuestion) {
			   
		//     }
		// },
		methods:{
			loadAll() {
			 	var result=[];
			 	reqData
			      .req({
			        apiName: "tuijian",
			        data: {			        
			          qiye_mc: this.companyName?this.companyName:','
			        }
			      })
			      .then(res => {
			        res = res.data;
			        if (res && res.code === "200") {
			          let data = res.data;
			          for(var i=0;i<data.length;i++){
			          	result.push({value:data[i][0],id:data[i][1]});
			          }
			          console.log(result);
			          // sessionStorage.setItem("tuijian", JSON.stringify(result));
			         
			        }
			      })
			      .catch(err => {
			        console.log(err);
			      });
			       return result;
		    },
		    querySearchAsync(queryString, cb) {
		        var restaurants = this.restaurants;
		        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

		        clearTimeout(this.timeout);
		        this.timeout = setTimeout(() => {
		          cb(results);
		        }, 10 * Math.random());
		    },
		    createStateFilter(queryString) {
		        return (state) => {
		          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
		        };
		    },
		    handleSelect(item) {
		        // console.log(item);
		        this.companyNames=item;
		        sessionStorage.setItem("company", JSON.stringify(this.companyNames));
		        // console.log(this.companyNames);
		    },
		    selectBtn(){
		      	if(this.companyName.trim()){
		      		if(this.companyNames.value!=this.companyName){
			        	sessionStorage.setItem("company", JSON.stringify({"value":this.companyName,"ID":"11111"}));
			        }
			        // sessionStorage.setItem("Neo4jToJson1", JSON.stringify([]));
			        // sessionStorage.setItem("Neo4jToJson2", JSON.stringify([]));
	                this.$store.commit('updateCompany');	
	                this.$store.commit('updateWord','');
	                // console.log(JSON.parse(this.$store.getters.getCompany));
	                this.$store.dispatch('updateWordCloud',JSON.parse(this.$store.getters.getCompany));	      		
	                this.$router.push({ name: 'inside' });
	                this.reload();
	                this.companyName='';
	            }else{
	            	alert('请输入公司名称')
	            }
		    }
		},
		mounted(){
			 this.restaurants = this.loadAll();
		},
		directives:{
			focus:{
				inserted:function(el) {			
					el.focus()
				}
			}
		},
	}
</script>
<style type="text/css" scoped="">
	.main .search{
		position: relative;
	}
	.main .search-txt{
		width: 100%;
		height: 36px;
		padding-right: 100px;
    	box-sizing: border-box;
		line-height: 36px;
		outline-style: none;
		border: none;
		font-size: 16px;
		color: #fff;
	}
	.main .search-btn{
		position: absolute;
		right: 0;
    	margin-top: -44px;
    	padding-right:20px;
		width: auto;
		height: 42px;
		font-size: 20px;
		line-height: 42px;
		background-color:transparent;
		border: none;
		color: #fff;
		text-indent: 15px;
		cursor: pointer;
	}
	.top .search-txt{
		width: 360px;
    	/*margin-left: 20px;*/
		height: 36px;
		line-height: 36px;
		outline-style: none;
		border: none;
		font-size: 16px;
		color: #333;
	}
	.top .search-btn{
		/* display: inline-block; */
	    font-size: 20px;
	    /*margin-left: 8px;*/
	    width: 78px;
	    border-radius: 10px;
	    height: 36px;
    	line-height: 36px;
	    background-color: transparent;
	    border: none;
	    color: #fff;
	    /* text-indent: 15px; */
	    cursor: pointer;
	}
	.search-txt::-webkit-input-placeholder {
	    color: #999999;
	    font-size: 16px;
	}

	.search-txt::-moz-placeholder {
	    color: #999999;
	    font-size: 16px;
	}

	.search-txt::placeholder {
	    color: #999999;
	    font-size: 16px;
	}
	/*搜索推荐*/
	#gov_search_suggest{ 
		position:absolute; 
		z-index:999;
		left:0; 
		top:48px;
		width: 100%; 
		border:1px solid #999999;
		background-color: #fff;
		max-height: 400px;
		overflow-y: auto;
		overflow-x: hidden;
		text-indent: 20px; 
		border-radius: 20px;
		display:none; 
	}
	.top #gov_search_suggest{
		top:42px;
	}
	#gov_search_suggest li{
		height:40px; 
		overflow:hidden;
		padding-left:3px; 
		line-height:40px; 
		/*background:#FFFFFF; */
		cursor:default;
	}
	#gov_search_suggest li.hover{
		color:red;
		cursor: pointer; 
	}
/*搜索推荐结束*/
</style>


