<template>
	<div class="main">
		<div class="header">
			<div class="contain">
				<div class="logo fl" style="margin-top: 18px;">
					<!-- <img src="../assets/logo.png"/> -->
				</div>
				<div class="login-box fr" style="margin-top: 6px;">
					<Login biaoshi="zhupingtai"/>
				</div>
			</div>
		</div>
		<div class="search-box">
			<Search show="true"/>
		</div>
		
	</div>
</template>
<script type="text/javascript">
	import Search from './search'
	import Login from './login'
	export default{
		name:'main',
		data(){
			return{
			}
		},
		components:{
			Search,
			Login
		},
		// computed:{
		// 	clientHeight:function() {
		// 		// alert(document.documentElement.clientHeight);
		// 		return document.documentElement.clientHeight;
		// 	}
		// }
	}
</script>
<style type="text/css" scoped="">
	.main{
		position: relative;
		width: 100%;
		position: absolute;
	    width: 100%;
	    height: 100%;
	    top: 0;
	    bottom: 0;
	    /*z-index: -1;*/
		background: url('../assets/image/mainBg2.png') no-repeat center 28%,
					url('../assets/image/mainBg.png') no-repeat center;
		background-size: 55%,cover;
		/*background: url('../assets/bg_text.png') no-repeat center 28%,
					/*url('../assets/main_bg2.png') no-repeat right -200%,*/
					/*url('../assets/main_bg3.png') no-repeat center 600px,
					url('../assets/main_bg1.png') repeat-x
		;
		background-size: inherit,inherit,contain;*/
	}
	.main .header{
		height: 80px;
		line-height: 80px;
		background-color: rgba(0,0,0,0.1);
		border-bottom: 1px solid rgba(255,255,255,0.2);
	}
	.main .search-box{
		width: 50%;
    	min-width: 400px;
		position: absolute;
		top: 60%;
		left: 50%;
		margin-left: -25%;
	}
	.main .search{
		width: 100%;
		height: 46px;
		line-height: 46px;
		background-color: rgba(0,0,0,0.12);
    	border: 1px solid rgba(255,255,255,0.6);
		/*background: url('../assets/search.png') no-repeat;
		background-size: cover;*/
	}
	.main .search .search-txt{
		float: left;
		margin-left: 20px;
		width: 70%;
		margin-left: 38px;
		height: 36px;
		line-height: 36px;
		outline-style: none;
		border: none;
		font-size: 16px;
		color: #fff;
	}
	.main .search .search-btn{
		float: right;
		margin-right: 20px;
		margin-left: 24px;
		width: 10%;
		height: 42px;
		line-height: 42px;
		background-color: #021945;
		border: none;
		color: #fff;
		text-indent: 15px;
		cursor: pointer;
	}
	.main .search-txt::-webkit-input-placeholder {
	    color: #fff;
	    font-size: 16px;
	}

	.main .search-txt::-moz-placeholder {
	    color: #fff;
	    font-size: 16px;
	}

	.main .search-txt::placeholder {
	    color: #fff;
	    font-size: 16px;
	}

</style>


